.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.banner-card {
  width : 100 vw;
  margin-top : 1vh;
  margin-bottom : 3vh;
  position : relative;
  height : 70vh;
  color:white;
}
.banner-img {
  height : 70vh;
  width : 100vw;
}

.banner-title {
  position: absolute;
  top : 45vh;
  left : 5vw;
}

.banner-text {
  position: absolute;
  top : 55vh;
  left : 5vw;
  height : 4.5rem;
  overflow : hidden ; /*after 4.5 rem text will hide*/
  margin-right : 5vw;
}

.movies-list{
  display : flex;
  flex-wrap:wrap;
  padding : 1rem;
  justify-content : center;
}

.movie-card{
  width:20vw;
  margin : 1rem;
  color : white;
  position: relative;
}

.movie-title{
  position: absolute;
  top : 1rem;
  left : 1rem;
}

.movie-button {
  position : absolute;
  bottom : 2rem;
}

.movie-img{
  height : 40vh;
  width : 20vw;
}

.pagination{
  display: flex;
  justify-content: center;
}

.favourites-list{
  padding: 3rem;
}
.favourites-table{
  padding: 3rem;
}


/* upto  400 px */
@media screen and (max-width: 400px) {
  .banner-card{
    height : 30vh;
  }
  .banner-img{
    height : 30vh;
  }
  .banner-title {
    top : 12vh;
    left: 5vw;
  }
  .banner-text{
    top : 17vh;
    left : 5vw;
  }

  .movie-card{
    width : 40vw;
    margin : 0.1rem;
  }
  .movie-button {
    width:80%;
  }
  .movie-img{
    width : 40vw;
  }
}




